<template>
  <div class="blog-box">
    <div class="content-box" v-if="contentBoxDirect==='left'">
      <div class="content-title" v-html="currentHtml"></div>
    </div>
    <div class="title-box">
      <div class="title-name">{{ content.title }}</div>
      <div class="banner-box">
        <slider ref="slider" :options="options" @slide="slider">
          <slideritem
              v-for="(item, index) in content.someList"
              :key="index"
              :style="item.style"
          >{{ item.title }}<br/>
            {{ item.author }}<br/>
            {{ item.creatTime }}
          </slideritem
          >
        </slider>
      </div>
    </div>
    <div class="content-box" v-if="contentBoxDirect==='right'">
      <div class="content-title" v-html="currentHtml"></div>
    </div>
  </div>
</template>

<script>
import {slider, slideritem} from "vue-concise-slider";

export default {
  components: {slider, slideritem},
  props: {
    contentBoxDirect: {
      type: String,
      default() {
        return "left";
      }
    },
    content: {
      type: Object,
      default() {
        return {
          title: "标题 title",
          someList: {
            title: "标题title",
            html: "内容html",
            author: "作者author",
            creatTime: "创建时间creatTime",
            style: {
              background:
                  'url("http://chaichaiimage.oss-cn-hangzhou.aliyuncs.com/blog3.0/bg12.jpg")',
              backgroundSize: "cover",
            }
          }
        };
      }
    },
  },
  data() {
    return {
      currentHtml: "currentHtml",
      options: {
        currentPage: 0,
        direction: "vertical",
        slidesToScroll: 1, //每次滑动项数
        effect: "fade",
        thresholdDistance: 10,
        loop: true,
      },
    };
  },
  methods: {
    mounted(){
      console.log(this.content)
      this.currentHtml = this.content.someList[0].html
    },
    slider(data) {
      this.currentHtml = this.content.someList[data.currentPage].html
      // console.log(data, 'silder1');
    },
  },
};
</script>

<style lang='scss'>
.blog-box {
  display: flex;
  flex-wrap: nowrap;
  width: 100%;
  height: 800px;
  box-shadow: 0 1px 10px 0 rgba(0, 0, 0, 0.1);
  margin-bottom: 20px;

  .title-box {
    width: 35%;
    height: 100%;
    text-align: center;

    .title-name {
      margin: 0 auto;
      letter-spacing: 2px;
      width: 80%;
      font-size: 22px;
      height: 50px;
      line-height: 50px;
      font-weight: 550;
    }

    .banner-box {
      width: 100%;
      height: 750px;

      .slider-item {
        color: rgb(255, 255, 255);
        font-weight: 550;
        line-height: 100px;
        text-shadow: 2px 2px 2px #000;
        font-size: 30px;
        user-select: none;
      }
    }
  }

  .content-box {
    width: 65%;
    height: 100%;
    background: #d0edfd;
  }
  .content-title{
    padding: 20px;
  }
}
</style>
