<template>
  <div class="about-box">
    <div class="main-box">
      <template v-for="(item,idx) in contentList">
        <banner-list v-bind:key="idx" :content="item" :content-box-direct="idx%2===0?'right':'left'"/>
      </template>
    </div>
  </div>
</template>

<script>
import bannerList from "./components/banner-list.vue";

export default {
  name: 'BlogView',
  components: {bannerList},
  data() {
    return {
      img: "http://chaichaiimage.oss-cn-hangzhou.aliyuncs.com/blog3.0/bg17.jpg",
      title: "博客",
      contentList: []
    };
  },
  mounted(){
    let that = this;
    this.$util.request.get("/resources/articles.json").then(function (data) {
      if (data) {
        that.contentList = data
      }
    });
  },
  methods:{
  }
};
</script>

<style lang="scss">
.about-box {
  height: 100%;

  .main-box {
    width: 70%;
    margin: 10px auto;
    padding-bottom: 10px;
  }
}
</style>

